.Image {
  overflow: hidden;
  display: inline-block;
  -ms-flex:1 1;
      flex:1 1;
  width: 100%;
  height: 100%;
}

.Image img {
  margin-bottom: -5px;
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
  width: 100%;
  height: auto;
}

.Image div {
  overflow: hidden;
  width:  100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

