.animation2 {
  -webkit-animation-name: test2;
          animation-name: test2;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

@-webkit-keyframes test2 {
  0% {
    -webkit-transform: translate(-1000px,1000px) rotate(0deg);
            transform: translate(-1000px,1000px) rotate(0deg);
  }
  0% {
    -webkit-transform: translate(-500px,500px) rotate(360deg);
            transform: translate(-500px,500px) rotate(360deg);
  }
  100% {
    -webkit-transform: translate(0px,0px) rotate(0deg);
            transform: translate(0px,0px) rotate(0deg);
  }
}

@keyframes test2 {
  0% {
    -webkit-transform: translate(-1000px,1000px) rotate(0deg);
            transform: translate(-1000px,1000px) rotate(0deg);
  }
  0% {
    -webkit-transform: translate(-500px,500px) rotate(360deg);
            transform: translate(-500px,500px) rotate(360deg);
  }
  100% {
    -webkit-transform: translate(0px,0px) rotate(0deg);
            transform: translate(0px,0px) rotate(0deg);
  }
}

