.Stack {
  width: 100%;
  height: 100%;
  display: inline-block;
}

.Stack .FadeIn {
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes fade {
  from {
    -webkit-filter: blur(3px);
            filter: blur(3px);
  }
  to {
  }
}

@keyframes fade {
  from {
    -webkit-filter: blur(3px);
            filter: blur(3px);
  }
  to {
  }
}


